@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  box-sizing: border-box;
}

body {
  min-width: 360px;
  background: #fff;
  color: #261f25;
  margin: 0;
  font: 16px/1.5 Montserrat, sans-serif;
}
@media (max-width: 1023px) {
  body {
    font-size: 14px;
  }
}

.justify-content-start {
  justify-content: flex-start !important;
}

.justify-content-center {
  justify-content: center !important;
}

.justify-content-between {
  justify-content: space-between !important;
}

.justify-content-end {
  justify-content: flex-end !important;
}

@media (min-width: 768px) {
  .flex-direction-row {
    flex-direction: row !important;
  }
}

@media (min-width: 768px) {
  .flex-direction-reverse {
    flex-direction: row-reverse !important;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.row-block {
  margin: -30px -15px 0;
  display: flex;
  flex-flow: row wrap;
}
@media (max-width: 1023px) {
  .row-block.column-straight-tablet [class^=col-block],
.row-block.column-straight-tablet [class*=" col-block"] {
    width: 100% !important;
  }
}
.row-block [class^=col-block],
.row-block [class*=" col-block"] {
  padding: 30px 15px 0;
  width: 100%;
}
@media (min-width: 768px) {
  .row-block > .col-block-1 {
    width: 8.3333333333%;
  }
  .row-block > .col-block-2 {
    width: 16.6666666667%;
  }
  .row-block > .col-block-3 {
    width: 25%;
  }
  .row-block > .col-block-4 {
    width: 33.3333333333%;
  }
  .row-block > .col-block-5 {
    width: 41.6666666667%;
  }
  .row-block > .col-block-6 {
    width: 50%;
  }
  .row-block > .col-block-7 {
    width: 58.3333333333%;
  }
  .row-block > .col-block-8 {
    width: 66.6666666667%;
  }
  .row-block > .col-block-9 {
    width: 75%;
  }
  .row-block > .col-block-10 {
    width: 83.3333333333%;
  }
  .row-block > .col-block-11 {
    width: 91.6666666667%;
  }
  .row-block > .col-block-12 {
    width: 100%;
  }
}

/* Forms Styles*/
form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

label {
  display: block;
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
  margin: 0 0 20px;
}

input[type=text],
input[type=tel],
input[type=email],
input[type=search],
input[type=password],
textarea,
.form-control {
  font: 500 14px/18px Montserrat, Arial;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  box-sizing: border-box;
  padding: 20px 27px;
  height: 65px;
  width: 100% !important;
  color: #261f25;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.07);
}
@media (max-width: 1023px) {
  input[type=text],
input[type=tel],
input[type=email],
input[type=search],
input[type=password],
textarea,
.form-control {
    padding: 15px 20px;
  }
}
input[type=text]:focus,
input[type=tel]:focus,
input[type=email]:focus,
input[type=search]:focus,
input[type=password]:focus,
textarea:focus,
.form-control:focus {
  outline: none;
  border-color: rgba(0, 0, 0, 0.2);
}
input[type=text]::placeholder,
input[type=tel]::placeholder,
input[type=email]::placeholder,
input[type=search]::placeholder,
input[type=password]::placeholder,
textarea::placeholder,
.form-control::placeholder {
  color: #999;
  opacity: 1;
}

textarea {
  min-height: 170px;
}

select {
  -webkit-border-radius: 0;
}

textarea {
  resize: vertical;
  vertical-align: top;
  resize: none;
  height: 170px;
}

button,
input[type=button],
input[type=reset],
input[type=file],
input[type=submit] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  cursor: pointer;
}

#wrapper {
  position: relative;
  overflow: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-lg {
  max-width: 1550px;
  margin: 0 auto;
  padding: 0 15px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #ca292d;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  margin: 0 0 20px;
  font-weight: 700;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}
h1 span,
h2 span,
h3 span,
h4 span,
h5 span,
h6 span {
  position: relative;
  color: #e03a29;
}

h1 {
  font-size: 72px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  h1 {
    font-size: 55px;
  }
}
@media (max-width: 1023px) {
  h1 {
    font-size: 45px;
  }
}
@media (max-width: 767px) {
  h1 {
    font-size: 35px;
  }
}

h2 {
  font-size: 42px;
  text-transform: uppercase;
  position: relative;
}
@media (max-width: 1199px) {
  h2 {
    font-size: 36px;
  }
}
@media (max-width: 1023px) {
  h2 {
    font-size: 30px;
  }
}
@media (max-width: 767px) {
  h2 {
    font-size: 25px;
  }
}
h2.border {
  padding: 0 0 40px;
}
h2.border::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  background: #ca292d;
  width: 100px;
  height: 8px;
}

h3 {
  font-size: 36px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  h3 {
    font-size: 30px;
  }
}
@media (max-width: 1023px) {
  h3 {
    font-size: 23px;
  }
}
@media (max-width: 767px) {
  h3 {
    font-size: 20px;
  }
}

h4 {
  font-size: 24px;
  text-transform: uppercase;
  margin: 0;
}
@media (max-width: 1023px) {
  h4 {
    font-size: 18px;
  }
}

h5 {
  font-size: 18px;
  margin: 0;
  line-height: 1.2;
  font-weight: 500;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  h5 {
    font-size: 16px;
  }
}

p,
ul {
  margin: 0 0 20px;
}
@media (max-width: 1023px) {
  p,
ul {
    margin: 0 0 15px;
  }
}
p:last-child,
ul:last-child {
  margin: 0;
}

a,
button,
input {
  transition: all 0.3s ease-in-out;
}

.btn {
  -webkit-appearance: none;
  font: 500 16px/22px Montserrat, Arial;
  outline: none !important;
  box-shadow: none;
  border: 1px solid transparent;
  border-radius: 0;
  position: relative;
  background: #e03a29;
  color: #fff;
  display: inline-flex;
  vertical-align: top;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 23px 20px;
  min-width: 200px;
  text-transform: uppercase;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .btn {
    font-size: 14px;
    padding: 15px;
    min-width: 150px;
  }
}
.btn.btn-sm {
  font-size: 14px;
  min-width: 170px;
  padding: 16px 15px;
}
.btn span {
  display: block;
  font-size: 14px;
  text-transform: lowercase;
}

.btn-primary {
  background-color: #ca292d;
}
.btn-primary:hover, .btn-primary:focus {
  background: #a02024;
}

.btn-white {
  background-color: #fff;
  color: #000;
  border-color: #eaeaea;
}

.btn-white:hover {
  background-color: #2f3186;
  color: #fff;
}

.full-width-bg {
  position: relative;
  z-index: 50;
}

.full-width-bg::before {
  background-color: #18356d;
  position: absolute;
  left: -9999px;
  top: 0;
  bottom: 0;
  z-index: -1;
  width: 9999px;
  content: "";
}

.header {
  width: 100%;
  position: absolute;
  left: 0;
  z-index: 5000;
  top: 0;
  background: rgba(255, 255, 255, 0.05);
}
@media (max-width: 767px) {
  .header {
    background: #18356d;
  }
}

.header .logo {
  background-color: #18356d;
  padding: 38px 20px 38px 0;
  position: relative;
  width: 277px;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .header .logo {
    width: 220px;
    padding: 15px 15px 15px 0;
  }
}

.header .top-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .top-header .navbar {
  flex-grow: 1;
  padding: 0 0 0 20px;
}
@media (max-width: 767px) {
  .header .top-header .navbar {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    background: #18356d;
    padding: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
  .nav-active .header .top-header .navbar {
    opacity: 1;
    visibility: visible;
  }
}

.header .top-header .navbar ul {
  margin: 0 -50px 0 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 1023px) {
  .header .top-header .navbar ul {
    margin: 0 -20px 0 0;
  }
}
@media (max-width: 767px) {
  .header .top-header .navbar ul {
    display: block;
  }
}

.header .top-header .navbar ul li {
  margin-right: 50px;
}
@media (max-width: 1023px) {
  .header .top-header .navbar ul li {
    margin: 0 20px 0 0;
  }
}
@media (max-width: 767px) {
  .header .top-header .navbar ul li {
    margin: 0;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
  }
}

.header .top-header .navbar ul li a {
  color: #fff;
  display: block;
  text-transform: uppercase;
  line-height: 1.2;
  font-weight: 500;
}
@media (max-width: 767px) {
  .header .top-header .navbar ul li a {
    padding: 15px;
  }
}

.header .top-header .navbar ul li a:hover,
.header .top-header .navbar ul li.active a {
  color: #e03a29;
}

.nav-opener {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  transition-property: right !important;
  background: #e03a29;
}
@media (min-width: 768px) {
  .nav-opener {
    display: none;
  }
}

.nav-opener::before,
.nav-opener::after,
.nav-opener span {
  width: 24px;
  height: 2px;
  background: #fff;
  content: "";
  display: block;
  transition: all 0.3s ease-in-out;
}

.nav-opener::before {
  margin-bottom: 6px;
}

.nav-active .nav-opener::before {
  margin-bottom: -2px;
  transform: rotate(45deg);
}

.nav-opener::after {
  margin-top: 6px;
}

.nav-active .nav-opener::after {
  margin-top: -2px;
  transform: rotate(-45deg);
}

.nav-active .nav-opener span {
  opacity: 0;
}

.nav-active {
  overflow: hidden;
}

.header-banner {
  overflow: hidden;
  position: relative;
  background-color: #3b5686;
  color: #fff;
}

.header-banner .banner-frame {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 100vh;
}

.header-banner.subpage .banner-frame {
  min-height: 570px;
}
@media (max-width: 1023px) {
  .header-banner.subpage .banner-frame {
    min-height: 400px;
  }
}
@media (max-width: 767px) {
  .header-banner.subpage .banner-frame {
    min-height: 300px;
  }
}

.header-banner .banner-body {
  padding: 202px 0 40px;
  width: 100%;
  position: relative;
  z-index: 50;
  font-size: 18px;
}
@media (max-width: 1023px) {
  .header-banner .banner-body {
    font-size: 16px;
    padding-top: 140px;
  }
}
.header-banner .banner-body h1:last-child {
  margin-bottom: 0;
}

.header-banner .banner-image {
  background-color: #3b5686;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.header-banner .banner-image img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  object-position: center;
}

.header-banner .btn-block {
  padding: 70px 0 0;
}
@media (max-width: 1023px) {
  .header-banner .btn-block {
    padding: 35px 0 0;
  }
}
@media (max-width: 767px) {
  .header-banner .btn-block {
    padding: 20px 0 0;
  }
}

.section-content {
  position: relative;
  padding: 85px 0;
  min-height: 575px;
  background: #18356d;
  color: #fff;
  z-index: 50;
}
@media (max-width: 1199px) {
  .section-content {
    padding: 60px 0;
  }
}
@media (max-width: 1023px) {
  .section-content {
    padding: 40px 0;
    min-height: inherit;
  }
}
.section-content h2 {
  font-weight: 800;
}
.section-content::after {
  position: absolute;
  left: 50%;
  right: 0;
  top: 0;
  bottom: 0;
  content: "";
  background: #1d3c77;
  z-index: -1;
}
@media (max-width: 1023px) {
  .section-content::after {
    display: none;
  }
}
.section-content .block-frame {
  margin: 0 -65px;
  display: flex;
  align-items: center;
}
@media (max-width: 1199px) {
  .section-content .block-frame {
    margin: -30px -30px 0;
  }
}
@media (max-width: 1023px) {
  .section-content .block-frame {
    display: block;
  }
}
.section-content .block-holder {
  position: relative;
  padding: 0 65px;
  width: 50%;
}
@media (max-width: 1199px) {
  .section-content .block-holder {
    padding: 30px 30px 0;
  }
}
@media (max-width: 1023px) {
  .section-content .block-holder {
    width: 100%;
  }
}
.section-content p {
  text-align: justify;
}
.section-content.reverse {
  background: #c22327;
}
.section-content.reverse .block-frame {
  flex-flow: row-reverse nowrap;
}
.section-content.reverse::after {
  background: #ca292d;
}
.section-content.reverse h2::after {
  background: #18356d;
}
.section-content .paragraph-heading {
  padding: 0 0 28px;
}
@media (max-width: 1023px) {
  .section-content .paragraph-heading {
    padding: 0 0 20px;
  }
}

.section.workshop-summary {
  padding: 150px 0;
  background: #18356d;
  color: #fff;
}
@media (max-width: 1199px) {
  .section.workshop-summary {
    padding: 100px 0;
  }
}
@media (max-width: 1023px) {
  .section.workshop-summary {
    padding: 50px 0;
  }
}

.section.workshop-summary .section-header {
  margin: 0 auto 100px;
  max-width: 845px;
  text-align: center;
}
@media (max-width: 1199px) {
  .section.workshop-summary .section-header {
    margin-bottom: 70px;
  }
}
@media (max-width: 1023px) {
  .section.workshop-summary .section-header {
    margin-bottom: 40px;
  }
}

.section.workshop-summary .workshop-grid {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.workshop-grid .workshop-block {
  padding: 12px 6px 0;
  width: 33%;
  display: flex;
  flex-flow: row wrap;
}
@media (max-width: 767px) {
  .workshop-grid .workshop-block {
    width: 100%;
  }
}

.workshop-grid .workshop-block .workshop-holder {
  width: 100%;
  display: flex;
  color: inherit;
  background-color: #112d63;
  padding: 40px 27px;
  min-height: 320px;
  color: #fff;
  flex-flow: row wrap;
}
@media (max-width: 1023px) {
  .workshop-grid .workshop-block .workshop-holder {
    padding: 30px 20px;
    min-height: 270px;
  }
}

.workshop-grid .workshop-block .workshop-holder .duration {
  align-self: flex-end;
  width: 100%;
  font-size: 22px;
  text-transform: uppercase;
  color: #34589d;
  font-weight: 700;
}
@media (max-width: 1023px) {
  .workshop-grid .workshop-block .workshop-holder .duration {
    font-size: 16px;
  }
}

.workshop-grid .workshop-block h4 {
  transition: all 0.3s ease-in-out;
}

.workshop-grid .workshop-block:hover h4 {
  color: #ca292d;
}

.section.workshop-summary .btn-block {
  padding: 120px 0 0;
  text-align: center;
}
@media (max-width: 1199px) {
  .section.workshop-summary .btn-block {
    padding: 80px 0 0;
  }
}
@media (max-width: 1023px) {
  .section.workshop-summary .btn-block {
    padding: 40px 0 0;
  }
}

.section.testimonials {
  padding: 135px 0;
  background-color: #f1f6ff;
}
@media (max-width: 1199px) {
  .section.testimonials {
    padding: 80px 0;
  }
}
@media (max-width: 1023px) {
  .section.testimonials {
    padding: 50px 0;
  }
}

.testimonail-block .btn-block {
  width: 100%;
  align-self: flex-end;
  padding: 57px 0 0;
}
@media (max-width: 1199px) {
  .testimonail-block .btn-block {
    padding: 40px 0 0;
  }
}
@media (max-width: 767px) {
  .testimonail-block .btn-block {
    padding: 30px 0 0;
  }
}

.testimonail-block .btn-block .btn {
  min-height: 80px;
  min-width: 260px;
  display: inline-flex;
  font-size: 18px;
  text-align: left;
  padding: 10px 32px;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
}

.testimonail-block:last-child .btn-block .btn {
  align-items: flex-start;
}

.section.testimonials .testimonial-grid {
  display: flex;
  margin: -30px -15px 0;
}
@media (max-width: 1023px) {
  .section.testimonials .testimonial-grid {
    display: block;
  }
}

.testimonial-grid .testimonail-block {
  display: flex;
  color: #18356d;
  position: relative;
  padding: 30px 15px 0;
  width: 50%;
}
@media (max-width: 1023px) {
  .testimonial-grid .testimonail-block {
    width: 100%;
  }
}
.testimonial-grid .testimonail-block p {
  font-weight: 300;
}

.testimonial-grid .testimonail-block .block-holder {
  background-color: #fff;
  width: 100%;
  padding: 60px 30px 0;
  position: relative;
  display: flex;
  flex-flow: row wrap;
}

.testimonail-block .block-header {
  min-height: 108px;
  margin: 0 0 30px;
  max-width: 70%;
}
@media (max-width: 1023px) {
  .testimonail-block .block-header {
    min-height: inherit;
    max-width: 100%;
  }
}

.testimonail-block .block-header h5:first-child {
  color: #d14649;
}

.testimonail-block .block-header h3,
.testimonail-block .block-header h5 {
  color: #18356d;
  margin: 0;
}

.testimonail-block .message-block p {
  margin: 0 0 20px;
  text-align: justify;
  font-weight: 400;
}

.testimonail-block .message-block p:last-child {
  margin: 0;
}

.footer {
  background: #18356d;
  font-size: 14px;
  line-height: 2;
  color: #fff;
  font-weight: 500;
}

@media (max-width: 1023px) {
  .footer {
    font-size: 13px;
    line-height: 1.6;
  }
}
.footer ul {
  list-style: none;
  padding: 0;
}

@media (max-width: 767px) {
  .footer .logo {
    margin: 0 0 20px;
  }
}
.footer .logo a {
  margin: 0;
}

.pri-footer {
  padding: 35px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

@media (max-width: 767px) {
  .pri-footer {
    padding: 25px 0 5px;
  }
}
.pri-footer .container {
  display: flex;
  align-items: center;
}

@media (max-width: 767px) {
  .pri-footer .container {
    flex-flow: row wrap;
  }
}
.additional-info {
  display: flex;
  margin: 0 0 4px;
  text-transform: uppercase;
}

@media (max-width: 767px) {
  .additional-info {
    width: calc(100% + 30px);
    flex-flow: row wrap;
    margin: 0 -15px;
  }
}
.additional-info li {
  margin: 0 57px;
}

@media (max-width: 1023px) {
  .additional-info li {
    margin: 0 0 0 50px;
